.promoImg{
    width: 100%;
    height: 380px;
    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;
}

.promoImg::before{
    content: "";
    height: inherit;
    width: 100%;
    position: absolute;
    background: #0b0b0b;
    opacity: .4;
}

.promoImgMini{
    width: 100%;
    height: 380px;
    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;
    box-shadow: 0 0 5px;
}

.aboutCont{
    display: flex;
    flex-wrap: wrap;
