.cont {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    max-width: 350px;
    width: 100%;
    flex-direction: column;

    a {
        div {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            color: #fff;
            padding: 0 10px;
        }
    }
}