.contRoom {
    width: 100%;
    display: block!important;
    max-width: 500px;
}

.descriptionCont {
    padding: 22px 5px 15px;

    h2 {
        text-align: center;
    }
}

.imgRoomTitle {
    width: 100%;
    max-width: 500px;
    height: 250px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.contBtn {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    align-items: center;

    .btn {
        text-align: center;
        padding: 8px 0;
        width: calc(50% - 50px);
        margin: 15px auto 10px;
    }
}

.termsCont {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: flex-start;
    margin: 25px 0!important;

    .termItem {
        max-width: 110px;
        width: 100%;
        text-align: center;
        min-height: 70px;

        span {
            font-size: 13px;
        }
    }

    .termImg {
        width: 25px;
        height: 25px;
        margin: auto;

        img {
            width: 25px;
            margin: auto;
            height: fit-content;

        }
    }
}

.text {
    font-size: 14px;
    text-align: center;
    padding: 10px 15px;
}
.btnInFull{
    text-align: center;
    width: 180px;
    padding: 8px 0px;
    margin: 15px auto 10px;
    color: rgb(255, 255, 255);
    border-color: rgba(255, 255, 255, 0);
}