.cont {
    width: 100%;
    display: flex;
    align-items: center;
    min-height: 100px;
    height: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    hr{
        width: calc(100% - 50px);
        margin: 10px 0;
    }
}

.protectionCont {
    display: flex;
    width: calc(100% - 100px);
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    .protection{
        margin: 15px;
    }
}

.prewTACont {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    justify-content: flex-start;

    .TACont {
        background-color: rgba(0, 0, 0, 0.425);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        height: 50px;
        width: 50px;
    }

    .TAItem {
        border: ridge 1px #fff;
        width: 17px;
        height: 17px;
        text-align: center;
        font-size: 12px;
        font-weight: bold;
        color:#fff
    }
}
.linkTA{
    color: #fff;
    .online{
        font-size: 10px;
    }
    margin: 15px;
}
.infoBlock{
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0;
    width: calc(100% - 100px);
}