.cont{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
}
.promoImg{
    width: 100%;
    height: 185px;
    background-size: cover;
    background-position: center;
}
.contBlock{
    width: 100%;
    max-width: 320px;
    box-shadow: 0 0 5px;
    margin: 10px 0 15px;
}
.hr{
    margin: auto;
    width: 70%;
    height: 0px;
}
.titleText{
    height: 65px;
    padding: 5px 10px;
    text-align: center;
    font-size: 22px;
}
.btn{
    
    background-color: mediumslateblue;
    text-align: center;
    padding: 8px 0;
    width: calc(100% - 50px);
    margin: 15px auto 10px;
}
.btnA{
    color: #fff;    
}
.about{
    height: 80px;
    padding: 5px 10px;
    text-align: center;
    font-size: 14px;
}
.termOfAction{
    font-size: 12px;
    text-align: center;
}