* {
    padding: 0;
    margin: 0;
    position: relative;
}

.main-cont {
    height: auto;
    margin: auto;
    width: 100%;
    max-width: 1170px;
}

.wdCont{
    height: auto;
    margin: auto;
    width: 100%;
    left: 0px;
    right: 0px;
    bottom: 10px;
    max-width: 1170px;
    position: absolute;

}

.slick-dots li {
    width: 100px !important;
    height: 100px !important;
    margin: 0px 5px 20px 5px !important;
    opacity: .65;
    border-radius: 50%;
    transition: .5s;
}

.slick-active {
    opacity: 1 !important;
    transition: .5s;
    border-radius: 50%;
}

.slick-dots li img {
    border-radius: 50%;
    height: 80px;
    width: 80px;
}

.left {
    flex-direction: row;
    .room_descriptionCont__1q_HS{
        right: 0;
    }
}

.right {
    flex-direction: row-reverse;
    .room_descriptionCont__1q_HS{
        left: 0;
    }
}

.right>.room_descriptionCont__PcvwD {
    left: 0;
    position: absolute;
}

.left>.room_descriptionCont__PcvwD {
    right: 0;
    position: absolute;
}
.roomSlides{
    margin-bottom: 100px;
}

.breadCrumbsCont{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
