.push {
    list-style: none;
    font-family: "Lucida Sans";
  }
  .push li {
    position: relative;
    padding: 22px 0 10px 20px;
    // color: #D29D25;
    font-variant: small-caps;
    font-weight: bold;
    cursor: pointer;
  }
  .push li:before {
    position: absolute;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: #4F5151;
    content: "";
    left: 0;
    transition: .3s ease-in-out;
    top: 27px;
  }
  .push li:after {
    position: absolute;
    border-left: 1px dotted #4F5151;
    width: 1px;
    bottom: -12px;
    content: "";
    left: 3px;
    top: 48px;
  }
  .push li:hover:before{box-shadow: 0 0 0 10px rgba(0,0,0,.2)}
  .push li:last-child:after {content: none;}


  .tag{
    padding: 15px 20px;
    background-color: rgb(42, 151, 151);
    border: 0;
    margin:25px 10px;
    color: #fff;

  }

  .tag.active{
    background-color: rgb(52, 218, 218);
        color:#000;
  }

.contPhotoGalary{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 100%;
    a{
            img{
                box-shadow: 0 0 7px black;
                transition: .5s;
                object-fit: cover;
                margin: 10px;
            }
            img:hover{
                box-shadow:none;
                transition: .5s;
            }
    }
    
}

