.imgPromotion {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 200px;
}

.cont {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: center;
}

.contItem {
    max-width: 300px;
    width: 100%;
    box-shadow: 0 0 5px;
    margin: 15px 0;
    text-align: center;

}

.textZona {
    padding: 10px 5px 3px;

    .textTitle {
        font-size: 1rem;
        height: 50px;
    }

    .textAbout {
        height: 100px;
        color: #878787;

        p {
            padding-top: 10px;
            padding-left: 15px;
            padding-right: 15px;
        }
    }
}

.btn {
    width: 80%;
    text-align: center;
    margin: auto;

    p {
        padding: 10px 0;
        width: 80%;
        margin: 5px auto 10px;
    }
}