.imgRoomTitle {
    width: 100%;
    height: 450px;
    background-size: cover;
    background-position: center;
    box-shadow: inset 0 0 10px black;
}

.imgRoomLink {
    width: 100%;
    max-width: calc(100% - 25%);
    z-index: 1;
    position: absolute;
}

.cont {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin: 50px auto;
}

.contRoom {
    position: relative;
    margin: 25px 0;
    width: 100%;
    min-height: 450px;
    display: flex;
    align-items: center;
}

.descriptionCont {
    display: flex;
    padding: 0 15px 0 25px;
    box-shadow: 0 0 5px;
    width: 100%;
    max-width: 500px;
    min-height: 400px;
    max-height: 400px;
    z-index: 2;
    flex-direction: column;
    justify-content: space-evenly;
    position: absolute;
}

.descriptionCont p {
    font-size: 14px;
}

.contBtn {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.btnBlock {}

.btn {
    width: 120px;
    background-color: mediumslateblue;
    padding: 8px 16px;
    margin: 15px 10px;
    text-align: center;
}

.termsCont {
    display: flex;
    align-items: flex-start;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.termAbout {
    font-size: 12px;
    width: 100%;
    max-width: 90px;
    text-align: center;
}

.termImg {
    background-size: cover;
    background-position: center;
    height: 25px;
    width: 25px;
    margin: auto;
}