.PrewImg{
    width: 100%;
    height: 85vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    .title{
        
        h2{
            font-size: 25px;
        }
    }
}
.PrewImg::before {
    content: "";
    height: 85vh;
    width: 100%;
    position: absolute;
    background: #0b0b0b;
    opacity: .6;
}