.hotelSCont{
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 100%;
}
.hSBlock{
    text-align: center;
    width: 100%;
    max-width: 275px;
    padding: 20px 5px;
    transition: .5s;
}
.hSBlock:hover{
    box-shadow: inset 0 0 5px black;
    transition: .5s;
}