.cont{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    align-content: center;
    width: 100%;
    max-width: 270px;

}
.img{
    box-shadow: 0 0 5px;
    border-radius: 22%;
    transition: all 6.5s ease 0s;
}
.img:hover{
    box-shadow:inset 0 0 5px;  
}