.sliderImgBig {
    width: 100%;
    max-width: 850px;
    height: 500px;
    margin: auto;
    display: block !important;

    .sliderImg {
        width: 100%;
        max-width: 850px;
        height: 500px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
}

.sliderMinImg {

    .sliderMin {
        width: 100%;
        max-width: 200px;
        height: 125px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        margin: auto;
    }
}

.dotsRooms {
    width: 15px;
    height: 15px;
    background-color: #60a4ac;
    margin: auto;
}

.styleLi {
    li {
        margin: 0 !important;
        top: 30px;
    }
}