.navMenu {
    display: flex;
    flex-wrap: wrap;
    margin: 20px 0 10px 0;
}

.burgerMenu {
    width: 40px;
    height: 40px;
    position: relative;
    cursor: pointer;
    background-color: #fff;

}

/* .burgerMenu {
    display: none;
} */

.item {
    padding: 5px 7px;
    min-height: 25px;
    color: #fff;
    line-height: unset;
}

.item:hover {
    background-color: rgba(255, 255, 255, 0.38);
    border-radius: 4px;
}

a {
    text-decoration: none;
}

.activeLink {
    color: #fff;
    background-color: rgba(255, 255, 255, 0.18);
    border-radius: 4px;
}

