.contLog {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    align-content: center;
}

.logText {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.t {
    border: ridge 2px;
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 50px;
    font-size: 45px;
}

.logoLinkA {
    div {
        .contLog {
            .contNameHotel {
                h2:after {
                    content: ' ';
                    display: none;
                    position: absolute;
                    bottom: 0;
                    width: 100%;
                    background: #00aeef;
                    left: 10px;
                    height: 3px;
                }
            }
        }
    }
}

.logoLinkA:hover {
    div {
        .contLog {
            .contNameHotel {
                h2:after, h2:before {
                    content: "";
                    position: absolute;
                    left: calc(100% + 15px);
                    top: 50%;
                    display: flex;
                    max-width: 50px;
                    width: 100%;
                    height: 2px;
                    background: #d9ae65;
                    pointer-events: none;
                }
            }
        }
    }
}

.logoLinkA:focus {
    color: #551a8b;
}

.contNameHotel {
    padding-left: 15px;
    margin-top: 12px;
    text-align: left;
}

@media (max-width:900px) {
    .contNameHotel {
        padding-left: 0;
        margin-top: 12px;
        text-align: center;
    }
}