.blue {
    background-color: #2196F3 !important
}

.blue.lighten-1 {
    background-color: #42A5F5 !important;
    transition: all 0.5s;
}
.blue.lighten-1:hover {
    background-color: #1e7bc7 !important
}
.blue.lighten-2 {
    background-color: #64B5F6 !important
}
.blue.lighten-3 {
    background-color: #90CAF9 !important
}
.blue.lighten-4 {
    background-color: #BBDEFB !important
}
.blue.lighten-5 {
    background-color: #E3F2FD !important
}
.blue.lighten-6 {
    background-color: #64b4f600 !important
}


.blue.darken-1 {
    background-color: #5380a7 !important
}
.blue.darken-2 {
    background-color: #1976D2 !important
}
.blue.darken-3 {
    background-color: #1565C0 !important
}
.blue.darken-4 {
    background-color: #0D47A1 !important
}


.blue.accent-1 {
    background-color: #82B1FF !important
}
.blue.accent-2 {
    background-color: #448AFF !important
}
.blue.accent-3 {
    background-color: #2979FF !important
}
.blue.accent-4 {
    background-color: #2962FF !important
}



.blue-text {
    color: #2196F3 !important
}
.blue-text.text-lighten-1 {
    color: #42A5F5 !important
}
.blue-text.text-lighten-2 {
    color: #64B5F6 !important
}
.blue-text.text-lighten-3 {
    color: #90CAF9 !important
}
.blue-text.text-lighten-4 {
    color: #BBDEFB !important
}
.blue-text.text-lighten-5 {
    color: #E3F2FD !important
}
.blue-text.text-lighten-6 {
    color: #ffffff !important
}


.blue-text.text-darken-1 {
    color: #1E88E5 !important
}
.blue-text.text-darken-2 {
    color: #1976D2 !important
}
.blue-text.text-darken-3 {
    color: #1565C0 !important
}
.blue-text.text-darken-4 {
    color: #0D47A1 !important
}


.blue-text.text-accent-1 {
    color: #82B1FF !important
}
.blue-text.text-accent-2 {
    color: #448AFF !important
}
.blue-text.text-accent-3 {
    color: #2979FF !important
}
.blue-text.text-accent-4 {
    color: #14358d !important
}



.blue-fill {
    fill: #2196F3 !important
}
.blue-fill.fill-lighten-1 {
    fill: #42A5F5 !important
}
.blue-fill.fill-lighten-2 {
    fill: #64B5F6 !important
}
.blue-fill.fill-lighten-3 {
    fill: #90CAF9 !important
}
.blue-fill.fill-lighten-4 {
    fill: #BBDEFB !important;
    transition: all 0.5s;
}
.blue-fill.fill-lighten-4:hover {
    fill: #2196F3 !important
}
.blue-fill.fill-lighten-5 {
    fill: #E3F2FD !important;
    box-shadow: 0 0 5px black;
}


.blue-fill.fill-darken-1 {
    fill: #1E88E5 !important
}
.blue-fill.fill-darken-2 {
    fill: #1976D2 !important
}
.blue-fill.fill-darken-3 {
    fill: #1565C0 !important
}
.blue-fill.fill-darken-4 {
    fill: #0D47A1 !important
}


.blue-fill.fill-accent-1 {
    fill: #82B1FF !important
}
.blue-fill.fill-accent-2 {
    fill: #448AFF !important
}
.blue-fill.fill-accent-3 {
    fill: #2979FF !important
}