.cont{
    width: calc(100% - 30px);
    padding: 0 15px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    min-height: 106px;
    background-color: mediumslateblue;
    justify-content: space-between;
    position: absolute;
    z-index: 999;
}
.menu{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    align-content: center;
}
.logotype{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    align-content: center;
    padding: 15px 0 0 0;
}
