.bigImageBlock {
    background-size: cover;
    background-position: center;
    height: 100vh;
    background-repeat: no-repeat;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: nowrap;
    align-content: center;
    flex-direction: column;
}

.bigImageBlock::before {
    content: "";
    height: 100vh;
    width: 100%;
    position: absolute;
    background: #0b0b0b;
    opacity: .6;
}

.contTermsBlock {
    width: 100%;
    max-width: 600px;
   
}

.contTermsBlock h2 {
    font-size: 30px;
    margin: 35px 0;
}

.contTerms {
    display: flex;
    width: 100%;
    max-width: 600px;
    flex-direction: row;
    align-items: flex-start;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.contTerm {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
    width: 45px;
    height: 45px;
    margin: auto;
}

.contTerm img {
    filter: brightness(0) invert(1);
    margin: 10px 0;
}

.contTermItem {
    min-width: 130px;
    max-width: 135px;
    text-align: center;
    margin: 35px 0;
}

.contTermAbout {
    width: 100%;
}

.NORCont {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-around;
    align-items: center;
    padding: 50px 0;
}

.NORBlock {
    max-width: 550px;
    width: 100%;
    margin: 20px 0;
    h2{
        font-size: 25px;
    }
}

.TBACont {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    box-shadow: 0 0 5px;
    text-align:center;
    padding: 8px 15px 45px;
    margin-bottom: 60px;
    .TBABlock {
        max-width: 550px;
        width: 100%;
        margin: 20px 0;
        hr{
            width: 40%;
            margin: auto;
        }
        h3{
            font-size: 25px;
            margin: 15px 0 20px;
        }
        p{
            padding: 10px 20px;
        }
    }
}


.TAMCont{
    box-shadow: 0 0 5px;
    padding: 15px 20px;
}
.TAMItemsCont{
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-evenly;
    align-items: flex-start;
}
.TAMItem{
    width: 100%;
    max-width:275px;
    display: flex;
    align-items: baseline;
    justify-content: flex-start;
    flex-wrap: nowrap;
    p{
        padding-left: 10px;
    }
}
.btn{
    padding: 10px 15px;
    text-align: center;
}