* {
    outline: none;
}

.slick-slide:focus {
    outline: none;
}

.slides {
    outline: none;
    margin-bottom: 50px;
}

/*слайдер услуги*/
.servicesMiniSlides {
    position: relative;

    .slick-dots {
        li {
            width: 24px !important;
            height: 0px !important;
            margin: 0 5px 0 0 !important;
        }
    }

    .slick-arrow {
        position: absolute;
        top: 50%;
        z-index: 10;
        width: 40px;
        height: 40px;
    }

    .slick-arrow.slick-prev {
        left: 25px !important;
        background-image: url(https://icon-icons.com/downloadimage.php?id=103394&root=1496/SVG/&file=goprevious_103394.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    .slick-arrow.slick-prev:before {
        content: '';
    }

    .slick-arrow.slick-next {
        right: 25px !important;
        background-image: url(https://icon-icons.com/downloadimage.php?id=103393&root=1496/SVG/&file=gonext_103393.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    .slick-arrow.slick-next:before {
        content: '';
    }

    .slick-list {
        .slick-track {
            .slick-slide {
                div {
                    margin: auto;
                }
            }
        }
    }
}

