.menu {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    transform: translateX(-130%);
    transition: all 0.4s;
}

.menu.active {
    transform: translateX(0);
    z-index: 99999;
}

.blur {
    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(2px);
    position: absolute;
}

.menu__content {
    width: 30%;
    height: 100%;
    background-color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.6s;
}

.menu__header {
    padding: 10px;
    font-size: 2rem;
    color: #fff;
    border-bottom: 1px solid #fff;
    width: 100%;
    text-align: center;
}

