.slidImage {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100vh;    
}
.slidImage::before {
    content: "";
    height: 100vh;
    width: 100%;
    position: absolute;
    background: #0b0b0b;
    opacity: .6;
}