.contServ{
    width: 100%;
    max-width: 1100px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: center;
    justify-content: space-evenly;
    align-items: center;
    margin: 25px auto 30px;

}
.contItemServ{
    max-width: 320px;
    width: 100%;
    box-shadow: 0 0 5px;
    margin: 10px 0;
}

.descriptionCont{
text-align: center;
padding: 10px 15px ;
}

.imgTitle{
    width: 100%;
    height: 185px;
    background-size: cover;
    background-position: center;
}
.btn{
    background-color: mediumslateblue;
    text-align: center;
    padding: 8px 0;
    width: calc(100% - 50px);
    margin: 15px auto 10px;
}